import moment from 'moment'

// STEP 1
import sunriseAndSunsetData from './sunrise-sunset.json'

export const getMoment = (locationName) => {
  // STEP 2
  const location = sunriseAndSunsetData.find(
    (data) => data.locationName === locationName
  )

  // STEP 3
  if (!location) {
    throw new Error(` ${location} `)
  }

  // STEP 4
  const now = new Date()

  // STEP 5 "2019-10-08" 
  const nowDate = Intl.DateTimeFormat('zh-TW', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit'
  })
    .format(now)
    .replace(/\//g, '-')

  // STEP 6
  const locationDate = location?.time.find((time) => time.dataTime === nowDate)

  // STEP 7
  if (!locationDate) {
    throw new Error(` ${locationName}  ${nowDate} `)
  }

  // STEP 8TimeStamp
  const sunriseTimestamp = new Date(
    `${locationDate.dataTime} ${locationDate.sunrise}`
  ).getTime()
  const sunsetTimestamp = new Date(
    `${locationDate.dataTime} ${locationDate.sunset}`
  ).getTime()
  const nowTimeStamp = now.getTime()

  // STEP 9
  return sunriseTimestamp <= nowTimeStamp && nowTimeStamp <= sunsetTimestamp
    ? 'day'
    : 'night'
}

export const transTime = (theTime) => {
  if (theTime) {
    const timeZoneRegex = /([-+]\d{2}:\d{2})$/
    const utcArray = timeZoneRegex.exec(theTime)[0].split(':')
    let utc = (Math.abs(utcArray[0]) * 60 + Number(utcArray[1])) / 60
    if (utcArray[0].split('').includes('-')) {
      utc = -1 * utc
    }
    const currentTime = moment
      .utc(theTime)
      .utcOffset(utc)
      .format('dddd, MM/D YYYY, h:mm a')
    return currentTime
  }
}
