// tools
import styled from '@emotion/styled'
import data from '../utils/AccuWeather/topCity.json'
import { useRef } from 'react'
import { useDispatch } from 'react-redux'
import { switchPage } from '../store/slice/page'
import { switchLocation } from '../store/slice/location'

// styles
const WeatherSettingWrapper = styled.div`
  position: relative;
  min-width: 360px;
  box-shadow: ${({ theme }) => theme.boxShadow};
  background-color: ${({ theme }) => theme.foregroundColor};
  box-sizing: border-box;
  padding: 20px;
`

const Title = styled.div`
  font-size: 28px;
  color: ${({ theme }) => theme.titleColor};
  margin-bottom: 30px;
`

const StyledLabel = styled.label`
  display: block;
  font-size: 16px;
  color: ${({ theme }) => theme.textColor};
  margin-bottom: 15px;
`

const StyledSelect = styled.select`
  display: block;
  box-sizing: border-box;
  background: transparent;
  border: 1px solid ${({ theme }) => theme.textColor};
  outline: none;
  width: 100%;
  max-width: 100%;
  color: ${({ theme }) => theme.textColor};
  font-size: 16px;
  padding: 7px 10px;
  margin-bottom: 40px;
  -webkit-appearance: none;
  -moz-appearance: none;
  box-shadow: none;
  outline: 0;
`

const ButtonGroup = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;

  > button {
    display: flex;
    align-items: center;
    justify-content: center;
    white-space: nowrap;
    user-select: none;
    margin: 0;
    letter-spacing: 0.3px;
    line-height: 1;
    cursor: pointer;
    overflow: visible;
    text-transform: none;
    border: 1px solid transparent;
    background-color: transparent;
    height: 35px;
    width: 80px;
    border-radius: 5px;
    font-size: 14px;

    &:focus,
    &.focus {
      outline: 0;
      box-shadow: none;
    }

    &::-moz-focus-inner {
      padding: 0;
      border-style: none;
    }
  }
`

const Back = styled.button`
  && {
    color: ${({ theme }) => theme.textColor};
    border-color: ${({ theme }) => theme.textColor};
  }
`

const Save = styled.button`
  && {
    color: white;
    background-color: #40a9f3;
  }
`

const WeatherSetting = () => {
  const inputLocationRef = useRef(null)

  //
  const handleSave = () => {
    // console.log(inputLocationRef.current.value.split(','))
    const currentLocation = inputLocationRef.current.value.split(',')
    dispatch(
      switchLocation({
        city: currentLocation[0],
        key: currentLocation[1]
      })
    )
    localStorage.setItem('city', currentLocation[0])
    localStorage.setItem('key', currentLocation[1])
  }

  //
  const dispatch = useDispatch()

  //
  return (
    <WeatherSettingWrapper>
      <Title></Title>
      <StyledLabel htmlFor="location"></StyledLabel>

      <StyledSelect id="location" name="location" ref={inputLocationRef}>
        {/*  */}
        {data.map((location) => (
          <option
            value={`${location.EnglishName},${location.Key}`}
            key={location.Key}
          >
            {location.EnglishName}
          </option>
        ))}
      </StyledSelect>

      <ButtonGroup>
        <Back onClick={() => dispatch(switchPage({ value: 'WeatherCard' }))}>
          
        </Back>
        <Save onClick={handleSave}></Save>
      </ButtonGroup>
    </WeatherSettingWrapper>
  )
}

export default WeatherSetting
